<template>
    <div class="alphabet">
        <ul class="list" >
            <li class="item" v-for="(item,index) in cit" 
            :key="index" 
            @click="handleLetter"
            >
            {{index}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"CityAlphabet",
    props:{
        cit:{}
    },
    methods:{
        handleLetter(e){
            var letter = e.target.innerText;

            this.$emit("letterHandle",letter)
        }
    }
}
</script>
<style scoped>
    .alphabet{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .list{
        position: absolute;
        right: 10px;
        top:100px;
    }
    .list .item{
        line-height: 25px;
    }
</style>


