<template>
    <div class="city-search">
        <input type="text" class="search-input" placeholder="请输入城市名或拼音">
    </div>
</template>
<script>
export default {
    name:'CitySearch'
}
</script>
<style scoped>
    .city-search{
        height:40px;
        background-color: #00bcd4;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .search-input{
        height: 30px;
        width: 70%;
        border-radius: 10px;
        text-align: center;
        box-sizing: border-box;
        border: 1px solid #eee;
    }

</style>


